import React, { Component } from 'react';
//import Parallax from 'parallax-js';
import sr from '../../plugins/scrollreveal.js'

class About extends Component {
    
    componentDidMount() {
        const config = {
          origin: 'bottom',
          distance: '50px',
          easing: 'ease',
          duration: 900,
          delay: 100,
          scale: 1,
          mobile: true,
        };
        const config2 = {
          origin: 'bottom',
          distance: '50px',
          easing: 'ease',
          duration: 900,
          delay: 300,
          scale: 1,
          mobile: true,
        };
        sr.reveal([this.scene1,this.scene2], config);
        sr.reveal([this.sceneA,this.sceneB], config2);
        
        
        /*let parallaxInstance1 = new Parallax(this.scene1, {
            relativeInput: true,
            hoverOnly: true
        });
        let parallaxInstance2 = new Parallax(this.scene2, {
            relativeInput: true,
            hoverOnly: true
        });
        parallaxInstance1.friction(0.1, 0.1);
        parallaxInstance2.friction(0.1, 0.1);*/
    }
    
    render(){
        return (
            <div className="about">
                <div className="about-wrapper">
                
                    <div className="about__bachelor">
                        
                        <div className="about__bachelor-wrapper">
                            <img src="img/coder.png" alt="BigBang Studio Developer" className="coder-icon"/>
                            <p ref={node => (this.sceneB = node)}> Coder at </p>
                            <h3 ref={node => (this.scene2 = node)}>
                                <a href="https://bigbang.studio"> BigBang Studio </a>
                            </h3>
                        </div>
                    
                    </div>
                    
                    <div className="about__work">
                        
                        <div className="about__work-wrapper">
                            <img src="img/bachelor.png" alt="System Engineering Bachelor" className="bachelor-icon"/>
                            <p ref={node => (this.sceneA = node)}> Bachelor in </p>
                            <h3 ref={node => (this.scene1 = node)}> Systems Engineering </h3>
                        </div>
                        
                    </div>
                    
                </div>
            </div>
        );
        /*return (
            <div className="about">
                <div className="about-wrapper">
                    <div ref={node => (this.scene1 = node)}>
                        <div className="about__bachelor reveal" data-depth="0.8">
                            <img src="img/unan.png" alt="unan" />
                        </div>
                    </div>
                    <div ref={node => (this.scene2 = node)}>
                        <div className="about__work reveal" data-depth="0.8">
                            <img src="img/bigbang-studio.png" alt="bigbang-studio" />
                        </div>
                    </div>
                </div>
            </div>
        );*/
    }
}

export default About;



