import React, { Component } from 'react';
//import API from './api';

class Contact extends Component {
    
    state = { costumerName: '', companyName: '', email: '', phone: '', message: '' }
    
    /*handler = true;
    create = e => {
        e.preventDefault();
        if(this.handler) {
            this.handler=false;
            console.log(this.state);
            const mailObj = {
                name: `${this.state.costumerName} FROM: ${this.state.costumerName}`,
                email: this.state.email,
                phone: this.state.phone,
                msj: this.state.message
            };
            API.post(`api/mail-api.php`, mailObj )
                .then(res => {
                    console.log(res);
                    console.log(res.data);
                })
            
        } else {
            alert("Espere que la accin anterior concluya");
        }
    }*/
    
    handleInputChange = e => {
        let target = e.target;
        
        let name = target.name;
        let value = target.value;
        
        this.setState({
            [name]: value
        });
    }
    
    render() {
        return(
            <div className="contact">
                <div className="contact__wrapper">
                    <div className="contact__info">
                        <h1 className="contact__title"> CONTACT </h1>
                        <ul className="contact__detail">
                            <li> 
                                <div className="icon-container">
                                    <i className="fa fa-user-o"></i>
                                </div>
                                Jennier Alexander Cruz </li>
                            <li> 
                                <div className="icon-container">
                                    <i className="fa fa-phone"></i>
                                </div>
                                5808-2679 </li>
                            <li> 
                                <div className="icon-container">
                                    <i className="fa fa-envelope-o"></i>
                                </div>
                                jenniercruz90@gmail.com </li>
                            <li> 
                                <div className="icon-container">
                                    <i className="fa fa-map-marker"></i>
                                </div>
                                Estel, Nicaragua </li>
                            <li className="second-address"> Managua, Nicaragua </li>
                        </ul>
                    </div>
                    <div className="contact__form">
                        <form action="https://formspree.io/jenniercruz90@gmail.com" method="POST" >
                            <p className="text-center form-title"> Take the next move </p>
                            <div className="form-group">
                                
                                <input
                                    name="costumerName"
                                    type="text"
                                    placeholder="Name"
                                    className="form-control"
                                    value={ this.state.costumerName }
                                    onChange={ this.handleInputChange }
                                />
                            </div>
                            <div className="form-group">
                                
                                <input
                                    name="companyName"
                                    type="text"
                                    placeholder="Company Name"
                                    className="form-control"
                                    value={ this.state.companyName }
                                    onChange={ this.handleInputChange }
                                />
                            </div>
                            <div className="form-group">
                                
                                <input
                                    name="phone"
                                    type="text"
                                    placeholder="Phone"
                                    className="form-control"
                                    value={ this.state.phone }
                                    onChange={ this.handleInputChange }
                                />
                            </div>
                            <div className="form-group">
                                
                                <input
                                    name="email"
                                    type="text"
                                    placeholder="E-mail"
                                    className="form-control"
                                    value={ this.state.email }
                                    onChange={ this.handleInputChange }
                                />
                            </div>
                            <div className="form-group">
                                
                                <textarea
                                    name="message"
                                    placeholder="Message"
                                    className="form-control"
                                    value={ this.state.message }
                                    onChange={ this.handleInputChange }
                                ></textarea>
                            </div>
                            <div className="form-group">
                                <button
                                    className="form-button"
                                    onClick={ this.create }
                                > SEND </button>
                            </div>
                            
                        </form>
                    </div>
                </div>
            </div>
        );
    }
}

export default Contact;


