import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class Footer extends Component {
    render(){
        return(
            <footer className="footer">
                <div className="footer-wrapper">
                    <div className="footer__item footer__menu">
                        <div>
                            <img src="img/logo_white.png" alt="logo-white" className="footer_logo" />
                        </div>
                        <ul>
                            <li> <Link to="/jenniercruz/"> Home </Link> </li>
                            <li> <Link to="/jenniercruz/portfolio"> Portfolio </Link> </li>
                            <li> <Link to="/jenniercruz/"> About me </Link> </li>
                            <li> <Link to="/jenniercruz/contact"> Contact </Link> </li>
                        </ul>
                    </div>
                    <div className="footer__item partner">
                        <div>
                            <p> Proud Partner </p>
                            <a href="https://bigbang.studio" alt="BigBang Studio" target="_blank" rel="noopener noreferrer"><img src="img/bigbang_white.png" alt="bigbang partner"/></a>
                        </div>
                    </div>
                    <div className="footer__item footer_contact">
                        <ul>
                            <li>
                                <div className="icon-container">
                                    <i className="fa fa-phone"></i>
                                </div>
                                (+505) 5808-2679
                            </li>
                            <li>
                                <div className="icon-container">
                                    <i className="fa fa-envelope-o"></i>
                                </div>
                                jenniercruz90@gmail.com
                            </li>
                        </ul>
                    </div>
                </div>
            </footer>    
        )
    }
}

export default Footer;



