import React, { Component } from 'react';
import Swipe, { SwipeItem } from 'swipejs/react';
import sr from '../../plugins/scrollreveal.js'

class Work extends Component {
    componentDidMount(){
        const config = {
          origin: 'bottom',
          distance: '50px',
          easing: 'ease',
          duration: 900,
          delay: 100,
          scale: 1,
          mobile: true,
          reset: false
        };
        const config2 = {
          origin: 'bottom',
          distance: '50px',
          easing: 'ease',
          duration: 900,
          delay: 300,
          scale: 1,
          mobile: true,
          reset: false
        };
        sr.reveal(this.scene1, config);
        sr.reveal(this.scene2, config2);
        
        this.swipeEl.setup({continuous: true});
        
        if(window.innerWidth < 900){
            const work__slider = document.querySelectorAll('.work__slider')[0];
            const work__info_top = work__slider.clientHeight;
            const work__info = document.querySelectorAll('.work__info')[0];
            work__info.style.marginTop = `${work__info_top}px`;
        }
    }
    render(){
        return(
            <div className="work">
                <div className="work-wrapper">
                    
                    <div className="work__slider">
                        <Swipe ref={o => this.swipeEl = o}>
                            {
                                this.props.work.images.map( i => (<SwipeItem> <img src={i} alt="hola" key={ i }/> </SwipeItem>) )
                            }
                        </Swipe>
                    </div>
                    
                    <div className="work__info">
                        <div className="work__info-wrapper">
                            <h1 className="work-title"> { this.props.work.title } </h1>
                            <div className="work-detail" ref={ e => this.scene1 = e }>
                                <div>Year: <b>{ this.props.work.year }</b> </div>
                                <div>Type: <b>{ this.props.work.type }</b> </div> 
                            </div>
                            <div className="work-technologies" ref={ e => this.scene2 = e }>
                                <b> Made with: </b>
                                <ul>
                                    {
                                        this.props.work.technologies.map( (e) => (<li  key={ e } >{e}</li>)  )
                                    }
                                </ul>
                            </div>
                        </div>
                    </div>
                  
                </div>
            </div>
        );
    }
}

export default Work;


