import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class Navigation extends Component {
    showMenu = () => {
        document.querySelector('.navigation').classList.toggle('navigation-isVisible') ;
    }
    
    render() {
        return(
            <nav className="navigation">
                <div className="navigation-wrapper">
                    <div className="navigation__header">
                        <img src="img/logo_black.png" alt="logo white" className="navigation__logo"/>
                        <span onClick={ this.showMenu }> close </span>
                    </div>
                    <ul>
                        <li> <Link to='/jenniercruz/' onClick={ this.showMenu }> Home </Link> </li>
                        <li> <Link to='/jenniercruz/portfolio' onClick={ this.showMenu }> Portfolio </Link> </li>
                        <li> <Link to='/jenniercruz/' onClick={ this.showMenu }> About me </Link> </li>
                        <li> <Link to='/jenniercruz/contact' onClick={ this.showMenu }> Contact </Link> </li>
                    </ul>
                    <div className="navigation__social">
                        <a className="social-link" href="https://linkedin.com/in/jennier-cruz-b98825144" target="_blank" title="social" rel="noopener noreferrer"> <i className="fa fa-linkedin" aria-hidden="true"></i> </a> 
                        <a className="social-link" href="https://dribbble.com/jenniercruz" target="_blank" title="social" rel="noopener noreferrer"> <i className="fa fa-dribbble" aria-hidden="true"></i> </a> 
                        <a className="social-link" href="https://github.com/j-alex" target="_blank" title="social" rel="noopener noreferrer"> <i className="fa fa-github" aria-hidden="true"></i> </a> 
                        <a className="social-link" href="https://instagram.com/jennier_cruz" target="_blank" title="social" rel="noopener noreferrer"> <i className="fa fa-instagram" aria-hidden="true"></i> </a> 
                        <a className="social-link" href="https://twitter.com/jenniercruz" target="_blank" title="social" rel="noopener noreferrer"> <i className="fa fa-twitter" aria-hidden="true"></i> </a> 
                    </div>
                </div>
            </nav>
        )
    }
}

export default Navigation;



