import React, { Component } from 'react';
import sr from '../../plugins/scrollreveal.js'

class Whatido extends Component {
    componentDidMount() {
        const config = {
          origin: 'bottom',
          distance: '50px',
          easing: 'ease',
          duration: 900,
          delay: 100,
          scale: 1,
          mobile: true,
        };
        const config2 = {
          origin: 'bottom',
          distance: '50px',
          easing: 'ease',
          duration: 900,
          delay: 300,
          scale: 1,
          mobile: true,
        };
        sr.reveal([this.scene1,this.scene2], config);
        sr.reveal([this.sceneA,this.sceneB], config2);
    }
    render() {
        return(
            <div className="whatido">
                <div className="whatido-wrapper">
                    <div className="item-container">
                        <h3 className="whatido__title"> What I Do </h3>
                        <p className="whatido__text" ref={node => (this.scene2 = node)}> 
                            Design and Development of web sites
                            highly dynamic and interactive
                            that brings an immersive user experience;
                            considering the next quality variables.
                        </p>
                    </div>
                    <div className="item-container" ref={node => (this.sceneB = node)}>
                        <figure>
                            <img src="./img/responsive.png" alt="Responsive Web Design" className="whatido-icons"/>
                            <figcaption>
                                <h4> RESPONSIVE</h4>
                            </figcaption>
                        </figure>
                        <figure>
                            <img src="./img/seo.png" alt="Search Engine Optimization" className="whatido-icons"/>
                            <figcaption>
                                <h4> SEO </h4>
                            </figcaption>
                        </figure>
                        <figure>
                            <img src="./img/performance.png" alt="Web Site Load Performance" className="whatido-icons"/>
                            <figcaption>
                                <h4> PERFORMANCE </h4>
                            </figcaption>
                        </figure>
                        <figure>
                            <img src="./img/mobilefirst.png" alt="Responsive Web Design" className="whatido-icons"/>
                            <figcaption>
                                <h4> Mobie 1<sup>st</sup> </h4>
                            </figcaption>
                        </figure>
                        <figure>
                            <img src="./img/friendly.png" alt="Search Engine Optimization" className="whatido-icons"/>
                            <figcaption>
                                <h4> Mobile Friendly </h4>
                            </figcaption>
                        </figure>
                        <figure>
                            <img src="./img/pwa.png" alt="Web Site Load Performance" className="whatido-icons"/>
                            <figcaption>
                                <h4> Progressive Web App </h4>
                            </figcaption>
                        </figure>
                    </div>
                </div>
            </div>
        );
    }
}

export default Whatido;


