import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class Header extends Component {
    showMenu = () => {
        document.querySelector('.navigation').classList.toggle('navigation-isVisible');
    }
    render(){
        return(
            <header className="header header--shadow">
                <div className="header-wrapper">
                    <Link to='/jenniercruz/'>
                        <img src="img/logo.png" alt="jenniercruz" className="header__logo"/>
                    </Link>
                    <span className="menu-btn" onClick={ this.showMenu }>
                        <i className="fa fa-bars fa-x3" aria-hidden="true"></i>
                    </span>
                </div>
            </header>
        );
    }
}

export default Header;



