import React from 'react';
import { Link } from 'react-router-dom';

const WorkItem = ({ works }) => (
     <div className="portfolio__gallery">
        {
            works.map( work => (
                    <Link to={`/jenniercruz/portfolio/${work.url}`} className="work-item-wrapper" key={ work.id }>
                        <figure className="work-item">
                            <img src={ work.portada } alt="first" className="work-img" />
                            <figcaption>
                                <h4> { work.title } </h4>
                            </figcaption>
                        </figure>
                    </Link>
                )
            )
        }
     </div>
);
export default WorkItem;


